<template>
  <div class="outline-wrap">
    <div class="head">
      <span v-if="outLineTitle">上一级：{{outLineTitle}}</span>
    </div>
    <el-form class="create-form" ref="form" :model="form" :rules="rules" label-width="100px">
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="内容标题：">
            <el-input v-model="form.Title" clearable></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="教学目的：">
        <summer-note :eqIndex="0"></summer-note>
      </el-form-item>
      <el-form-item label="教学重点：">
        <summer-note :eqIndex="1"></summer-note>
      </el-form-item>
      <el-form-item label="教学难点：">
        <summer-note :eqIndex="2"></summer-note>
      </el-form-item>
      <el-form-item label="教学要求：">
        <summer-note :eqIndex="3"></summer-note>
      </el-form-item>
      <el-form-item label="内容描述：">
        <summer-note :eqIndex="4"></summer-note>
      </el-form-item>
      <el-form-item>
        <div class="btns">
          <el-button size="medium" type="primary" plain @click="onSubmit">保存</el-button>
          <el-button size="medium" type="info" plain>取消</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import http from "../../../../utils/http";
import api from "../../../../utils/api";
import summerNote from "../../../../components/personal/summernote.vue";
export default {
  components: {
    summerNote
  },
  data() {
    return {
      outLineTitle: "",
      isEdit: "",
      tcid: "",
      form: {
        Title: ""
      },
      rules: {
        Title: [
          {
            required: true,
            min: 2,
            max: 25,
            message: "长度在 2 到 25 个字符",
            trigger: "blur"
          }
        ]
      }
    };
  },
  mounted() {
    this.judgeSta();
  },
  methods: {
    judgeSta() {
      this.outLineTitle = this.$route.params.tpname;
      this.tcid = this.$route.query.tcid;
      if (this.tcid == "" || this.tcid == undefined) {
        this.isEdit = false;
        this.$set(this.form, "Title", "");
        $(".summernote")
          .eq(0)
          .summernote("code", "");
        $(".summernote")
          .eq(1)
          .summernote("code", "");
        $(".summernote")
          .eq(2)
          .summernote("code", "");
        $(".summernote")
          .eq(3)
          .summernote("code", "");
        $(".summernote")
          .eq(4)
          .summernote("code", "");
      } else {
        this.isEdit = true;
        this.getData();
      }
    },
    getData: async function() {
      const res = await http.get(api.getTeachingContentData, {
        id: this.tcid,
        Token: http.getToken()
      });
      if (res.Result) {
        if (res.Data == null) return;
        this.form.Title = res.Data.TCTitle;
        $(".summernote")
          .eq(0)
          .summernote("code", res.Data.TCAims);
        $(".summernote")
          .eq(1)
          .summernote("code", res.Data.TCKeyPoint);
        $(".summernote")
          .eq(2)
          .summernote("code", res.Data.TCDifficulty);
        $(".summernote")
          .eq(3)
          .summernote("code", res.Data.TCDemand);
        $(".summernote")
          .eq(4)
          .summernote("code", res.Data.TCDesc);
      }
    },
    onSubmit() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          let params = {
            tcid: this.tcid,
            tpid: this.$route.params.tpid,
            ptcid: this.$route.params.tcid,
            tctile: this.form.Title,
            tcaims: $(".summernote")
              .eq(0)
              .summernote("code"),
            tckp: $(".summernote")
              .eq(1)
              .summernote("code"),
            tcdc: $(".summernote")
              .eq(2)
              .summernote("code"),
            tcdemand: $(".summernote")
              .eq(3)
              .summernote("code"),
            tcdesc: $(".summernote")
              .eq(4)
              .summernote("code"),
            tcforce: "0"
          };
          this.saveData(params);
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
          return false;
        }
      });
    },
    saveData: async function(params) {
      const res = await http.post(api.saveTeachingContent, params);
      if (res.type) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "保存内容成功",
          type: "success"
        });
        if (!this.isEdit) {
          this.tcid = res.Data;
          this.$parent.$refs.nav.updata = true;
        }
        this.$router.push({
          name: "contentView",
          query: { tcid: this.tcid }
        });
      }
    }
  },
  watch: {
    $route(to, from) {
      this.judgeSta();
    }
  }
};
</script>
<style lang="less" scoped>
</style>



